@font-face {
  font-family: "Gotham Book";
  src: url(/static/media/GothamBook.b54724f5.ttf);
}

@font-face {
  font-family: "Gotham Light";
  src: url(/static/media/GothamLight.8566e233.ttf);
}

@font-face {
  font-family: "Gotham Medium";
  src: url(/static/media/GothamMedium.77171d8f.ttf);
}

@font-face {
  font-family: "Gotham Bold";
  src: url(/static/media/GothamBold.db33e70b.ttf);
}


body {
  background: rgb(24, 24, 24);
  font-family: Gotham Medium; 
  color: darkgray;
}

body::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: url(/static/media/background.123f6dfd.png) no-repeat;
  background-size: cover;
  background-position: center center;
  z-index: -1; 
  max-height: 720px;
}

