a {
  color: inherit;
  text-decoration: inherit;
}

a:hover {
  color: white;
}

.modal-title a:hover {
  color: black;
}

.black {
  color: black;
}

.cornflowerblue {
  color: cornflowerblue;
}

h1 {
  color: white;
  font-size: 5.0em;
}

h2 {
  font-family: Gotham Bold;
  font-size: 2.5em;
}

.heading {
  height: 650px;
}

.location-text {
  display: inline-block;
}

.modal-title {
  color: black;
}

.mt-6 {
  margin-top: 7rem;
}

.mt-10 {
  margin-top: 10rem;
}

.mt-15 {
  margin-top: 15rem;
}

.navbar {
  background: rgba(24, 24, 24, 1.0); 
  height: 7.0em;
  padding-left: 2em;
  padding-right: 2em;
}

.nav-item {
  margin-left: 0.5em;
  font-size: 1.8em;
  font-family: Gotham Medium; 
}

.navbar-nav {
  margin-left: auto;
}

.navbar-bottom {
  margin-left: auto;
}

.navbar-bottom-icon {
  height: 1.2em;
  margin-left: 2.5em; 
  margin-right: 0.5em;
}

.project-image {
  height: 275px;
  width: 275px;
  overflow: hidden;
  object-fit: cover;
}
